import { Component, OnInit } from '@angular/core';
import {Http, Response} from '@angular/http';

@Component({
  selector: 'app-pickup',
  templateUrl: './pickup.component.html',
  styleUrls: ['./pickup.component.css']
})
export class PickupComponent implements OnInit {

  pickupAddress: any;
  baseUrl: String = 'https://maps.googleapis.com/maps/api/geocode/json?address=ADDRESS&key=YOUR_API_KEY';
  key: any = 'AIzaSyBGG-wkSH660bvqdSp-zIByzIzYPGqvfiQ';
  result: any;
  constructor(private http: Http) { }

  ngOnInit() {
  }


  searchLocation() {
    const url = this.baseUrl
      .replace('YOUR_API_KEY', this.key)
      .replace('ADDRESS', this.pickupAddress);
    this.result = this.http.get(url);
  }
}



